
body {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  margin-top: 8%;
  background: #85E29A;
}
svg {
  background: #fff;
  box-shadow: 8px 9px 25px 4px rgba(0,37,0,0.58);
}
.bar {
  fill: #BCB6FF;
}
.bar:hover {
  fill: #B8E1FF;
}
.title {
  font-size: 30px;
  fill: #536D67;
}
.desc {
  font-size: 9px;
  fill: #536D67;
}
.tooltip {
  position: absolute;
  z-index: 10;
  visibility: hidden;
  background: #94FBAB;
  border-radius: 5px;
  padding: 5px;
  width: 140px;
  height: 40px;
  box-shadow: 8px 9px 25px 4px rgba(0,37,0,0.58);
}



